import React from 'react'
import cx from 'classnames'
// import Tooltip from 'Shared/components/Tooltip/Tooltip'
// import { isMobile } from 'Shared/helpers/mobile'
// import { t } from 'i18next'
import './ScoreTag.scss'

function ScoreTag (props) {
  if (!props.scoreData) return (<div>a</div>)
  const { title, handleOnClick, scoreData: { score } } = props

  // const tooltipEventType = isMobile() ? 'click' : 'mouseenter'

  const scoreClass = cx({
    'low': score <= 35,
    'medium': score >= 35 && score < 72,
    'high': score >= 72 && score <= 100
  })

  return (
    <div className={`score-tag ${scoreClass}`} onClick={() => handleOnClick()}>
      {/* <Tooltip trigger={tooltipEventType} content='tooltip content'> */}
      {scoreClass} {title} {score} <span className='icon'>i</span>
      {/* </Tooltip> */}
    </div>
  )
}

export default ScoreTag



